.field__lead {
  margin-left: 23px;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
  color: $color-primary;

  @media (min-width: $tablet-width) {
    margin-left: 0;

    &--tel,
    &--mail {
      margin-left: 18px;
    }
  }

  @media (min-width: $desktop-width) {
    &--tel,
    &--mail {
      margin-left: 20px;
    }
  }
}

.field__input {
  padding: 14px 23px;
  font-family: $font-body;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  border: none;
  outline: 2px solid $special-gray-200;
  color: $special-gray;
  border-radius: 3px;

  &:hover {
    outline: 2px solid $special-gray;
  }

  &:active,
  &:focus {
    outline: 2px solid $color-primary;
  }


}

.field__input::placeholder {
  color: $special-gray;
}

.field__input--textarea {
  margin-bottom: 48px;
  min-height: 200px;
  resize: none;
  @media (min-width: $tablet-width) {
    margin-right: 60px;
    margin-left: 60px;
    margin-top: 16px;
    margin-bottom: 52px;
    min-height: 190px;
    padding: 16px 20px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 13px;
    min-height: 124px;
    margin-bottom: 44px;
  }
}
