.developer {
  width: max-content;
  font-weight: 400;
  font-size: 14px;
  text-decoration: none;
  line-height: 34px;
  color: $special-gray;
  display: grid;
  grid-template-columns: max-content max-content;
  gap: 15px;
  align-items: center;

  @media (min-width: $desktop-width) {
    font-size: 18px;
  }

  svg {
    color: $color-primary;
  }

  &:hover svg {
    color: $special-gray;
  }

  &:active svg {
    color: fade-out($special-gray, 0.7);
  }
}
