@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans-400.woff2") format("woff2"), url("../fonts/opensans-400.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/opensans-300.woff2") format("woff2"), url("../fonts/opensans-300.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensans-700.woff2") format("woff2"), url("../fonts/opensans-700.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  width: max-content;
  height: max-content;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  border: none;
  font-weight: 700;
  text-align: center;
  font-size: 18px;
  text-decoration: none;
  background-color: transparent;
  cursor: pointer;
}

.button--primary {
  padding-top: 14px;
  padding-left: 14px;
  padding-right: 14px;
  padding-bottom: 13px;
  color: #ffffff;
  border: 3px solid #ffffff;
  border-radius: 29px;
}

.button--secondary {
  border: none;
  color: #ffffff;
  background-color: #2aab6d;
  border-radius: 3px;
  padding: 13px;
  text-transform: uppercase;
  width: 100%;
}

