.main-nav {
  z-index: 2;
  position: relative;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
}

.main-nav__bar {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 66px;
  border-bottom: 1px solid transparent;
  width: auto;
  padding-right: 20px;
  padding-left: 20px;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 660px) {
  .main-nav__bar {
    min-height: 110px;
  }
}

.main-nav__toggle {
  margin-left: auto;
  display: block;
  width: 50px;
  height: 50px;
  position: relative;
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 13px;
  right: 0;
  left: 0;
  height: 4px;
  border-radius: 1px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}

.main-nav__toggle:hover::after {
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}

.main-nav__toggle:active::after {
  box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
}

.main-nav__wrapper {
  display: none;
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    display: block;
  }
}

.main-nav--nojs {
  background-color: #283645;
}

.main-nav--nojs .main-nav__wrapper {
  display: block;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--opened {
  background-color: #283645;
}

.main-nav--opened .main-nav__bar {
  border-bottom: 1px solid #000000;
}

.main-nav--opened .main-nav__wrapper {
  z-index: 2;
  position: absolute;
  top: 99%;
  width: 100%;
  display: block;
}

.main-nav--opened .main-nav__toggle::after,
.main-nav--opened .main-nav__toggle::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 4px;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  box-shadow: none;
  margin: auto;
  background-color: #ffffff;
  transform: rotate(45deg);
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(-45deg);
}

.main-nav .main-nav__toggle:hover::before,
.main-nav .main-nav__toggle:hover::after {
  background-color: #d22856;
}

.main-nav .main-nav__toggle:active::before,
.main-nav .main-nav__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}

