  grid-row: 1/2;
  z-index: 1;
  width: 37px;
  height: 37px;
  margin: 0;
  opacity: 0;
  cursor: pointer;
}

.control__lead {
  align-items: end;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  grid-column: 1/-1;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 37px 1fr;
  gap: 22px;
}
