  grid-column: 1/2;
  grid-row: 1/2;
  content: "";
  width: 37px;
  height: 37px;
  background-color: #ffffff;
  outline: 2px solid #e5e5e5;
  border-radius: 1px;
}
.control__lead::after {
  grid-column: 1/2;
  grid-row: 1/2;
  width: 37px;
  height: 37px;
}

.control__lead:hover::before,
.control__input:hover + .control__lead::before {
  outline: 2px solid #283645;
}

