
.field-group {
  border: none;
  padding: 0;
  margin: 0;
  margin-top: -5px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
    margin-bottom: 70px;
    margin-right: 20px;
    margin-left: 20px;

    &:not(:first-child) {
      border: 2px solid $special-gray-200;
      padding-top: 0;
      border-radius: 3px;
    }

    &:last-of-type {
      margin-bottom: 46px;
    }
  }

  @media (min-width: $desktop-width) {
    &:not(.field-group--name):not(.field-group--app) {
      grid-column: 1 / -1;
    }

    &--textarea {
      margin-right: auto;
      margin-left: auto;
    }
  }
}

.field-group:nth-child(2n) {
  background-color: $special-gray-100;
  @media (min-width: $tablet-width) {
    background-color: $basic-white;
  }
}

.field-group__heading {
  margin-top: 0;

  padding: 0 20px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  color: $color-primary;

  @media (min-width: $tablet-width) {
    transform: translateY(-50%);
    background-color: $basic-white;
    width: fit-content;
  }
}

.field-group__heading--contacts {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.field-group__list {
  margin-top: 39px;
  list-style-type: none;
  margin-bottom: 48px;
  padding: 0;
  text-align: left;

  width: 280px; // 320 - 20 - 20
  margin-right: auto;
  margin-left: auto;

  &--contacts {
    margin-top: 32px;
  }

  @media (min-width: $tablet-width) {
    width: auto;
    padding-right: 60px;
    padding-left: 60px;

    &--name {
      padding-right: 0;
      padding-left: 0;
    }
    &--contacts {
      margin-top: 30px;
      margin-bottom: -26px;
    }

    &--progress {
      margin-top: 36px;
      margin-bottom: -5px;
    }
  }

  @media (min-width: $desktop-width) {
    &--contacts {
      margin-top: 26px;
      margin-bottom: -18px;
    }
  }
}

.field-group__item {
  margin-bottom: 19px;

  @media (min-width: $tablet-width) {

    margin-bottom: 57px;
    &:last-child {
      margin-bottom: 54px;
    }

    &--app {
      margin-bottom: 40px;
    }

    &--contacts {
      margin-bottom: 32px;
    }

    &--progress {
      margin-bottom: 20px;
    }
  }

  @media (min-width: $desktop-width) {
    &:last-child {
      margin-bottom: 0;
    }

    &--progress {
      margin-bottom: 1px;
    }
  }
}


.field-group__field {
  display: grid;
  gap: 8px;
}

.field-group--name {
  order: -3;
  @media (min-width: $tablet-width) {
    margin-bottom: 25px;
    .field-group__field {
      grid-template-columns: max-content 1fr;
      gap: 20px;
      align-items: center;
    }

    .field-group__item:last-child {
      margin-bottom: 0;
    }
  }

  @media (min-width: $desktop-width) {
    .field-group__list {
      width: auto;
      margin-top: 9px;
    }

    .field-group__item {
      margin-bottom: 58px;
    }
  }
}

.field-group--progress {
  @media (min-width: $desktop-width) {

    .field-group__list {
      display: grid;
      grid-template-columns: max-content max-content 1fr;
      grid-template-rows: 1fr 1fr;
      grid-auto-flow: column;
      column-gap: 55px;
      row-gap: 44px;
      margin-bottom: 54px;
    }
  }
}

.field-group--contacts {
  @media (min-width: $tablet-width) {
    order: -1;
    .field-group__field {
      display: flex;
      flex-direction: column-reverse;
      position: relative;
      gap: 12px;

      &::after {
        position: absolute;
        content: "";
        right: 0;
        top: 0;
        height: 58px;
        width: 58px;
        background-color: $special-gray-200;
        background-position: center;
        background-repeat: no-repeat;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;
      }
    }

    .field-group__field--tel::after {
      background-image: url("../img/icons/phone-icon.svg");
      background-size: 20px 19px;
    }

    .field-group__field--mail::after {
      background-image: url("../img/icons/mail-icon.svg");
      background-size: 18px 13px;
    }

    .field__input {
      padding-right: 70px;
    }
  }

  @media (min-width: $desktop-width) {
    .field-group__list {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 74px;
    }
  }
}

.field-group--app {
  @media (min-width: $tablet-width) {
    order: -2;
  }

  @media (min-width: $desktop-width) {
    .field-group__list {
      width: auto;
    }
  }

  @media (min-width: $desktop-width) {
    .field-group__list {
      padding-left: 72px;

    }
  }
}
